.hero {
  position: relative;
}
.hero h3 {
  color: rgba(198, 201, 216, 0.75) !important;
  font-weight: 100;
  letter-spacing: 2px;
}
.hero h1 {
  color: #ffffff;
  font-size: 60px;
  font-weight: bold;
}

.hero h1 span {
  color: #08fdd8;
}
.hero h2 {
  font-size: 50px;
  font-weight: bold;
  color: #08fdd8;
}
.hero h2 span {
  color: #ffffff;
}
.hero p {
  color: rgba(198, 201, 216, 0.75) !important;
  font-size: 18px;
  line-height: 30px;
  margin-top: 30px;
}
.hero_btn {
  margin-top: 15%;
}
.hero_btn h4 {
  font-weight: 500;
  letter-spacing: 2px;
  color: #ffffff;
  font-size: 15px;
  margin: 30px 0 30px 0;
}
.hero_btn button {
  margin-right: 15px;
  margin-top: 15px;
}
.hero_btn i {
  color: black;
  font-size: 20px;
}

.hero_btn button img {
  width: 20px;
  height: 20px;
}
.hero .left {
  margin-right: 10%;
}
.hero .right {
  width: 40%;
  position: relative;
}
.hero .right_img {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  border-radius: 10px;
  height: 580px;
  width: 510px;
  margin-top: 20%;
}
.hero .right_img img {
  position: absolute;
  top: 0;
  right: 10%;
  width: 510px;
}
@media (max-width: 768px) {
  /* max-width: 600px*/
  .hero .right,
  .hero .left {
    width: 95%;
    margin: auto;
  }

  .hero .f_flex {
    flex-direction: column;
    flex-direction: column-reverse;
  }
  .hero .right_img {
    width: 100%;
    height: 54vh;
  }
}
