header {
  height: 10vh;
  line-height: 10vh;
}
header .container {
  padding-top: 15px;
}
header ul li {
  margin-left: 30px;
}
header ul li a {
  transition: 0.5s;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
}
header ul li a:hover {
  color: #08fdd8;
}
.home-btn {
  padding: 18px 25px;
  border-radius: 6px;
  transition: 0.3s all ease;
  cursor: pointer;
  height: fit-content;
  color: #08fdd8;
}

/*
.home-btn {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
}*/

.home-btn:hover {
  background: #08fdd8;
  color: white;
}
.open {
  display: none;
}
.header.active {
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: #191919;
  box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.75);
  transition: 0.5s;
}

/*Taille maximum 810*/
@media (max-width: 770px) {
  header ul {
    padding-top: 30px;
  }
  .nav-links-mobile {
    position: absolute;
    display: block;
    list-style: none;
    box-shadow: rgba(50, 50, 93, 0.23) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    right: 0;
    top: 0;
    transition: all 0.5s ease-in-out;
    width: 30%;
    height: 100vh;
    background-color: #1d1d1d;
    z-index: 888;
  }
  header ul li {
    width: 100%;
    transition: all 0.5s ease-in-out;
    text-transform: uppercase;
    line-height: 70px;
  }
  header .link {
    display: none;
  }
  .close,
  .open {
    position: absolute;
    top: 15px;
    right: 20px;
    color: #08fdd8;
    display: block;
    font-size: 20px;
    z-index: 999;
  }
  .close {
    right: 20px;
  }
  .close.home-btn {
    background-color: #1d1d1d;
    color: #08fdd8;
    border-radius: 50%;
    padding: 0;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }
  .header.active {
    height: 10vh;
  }
}

